'use client';

import { Container, Title, Text, Button, Group, SimpleGrid, Card, Image, Box } from '@mantine/core';
import { IconCalendar, IconBuildingCommunity } from '@tabler/icons-react';
import { motion } from 'framer-motion';
import Link from 'next/link';

export default function Home() {
    return (
        <main>
            {/* Hero Section */}
            <Box
                component={motion.div}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
                style={{
                    height: '85vh',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    textAlign: 'center',
                    background: 'radial-gradient(circle at center, rgba(0,122,255,0.03) 0%, rgba(255,255,255,0) 70%)',
                }}
            >
                <motion.div
                    initial={{ y: 30, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ delay: 0.2, duration: 0.8, ease: "easeOut" }}
                >
                    <Title order={1} size="5rem" style={{ fontWeight: 800, letterSpacing: '-0.03em', marginBottom: '1.5rem', fontFamily: 'var(--font-sf)' }}>
                        Nonghyup Broadcasting
                    </Title>
                </motion.div>

                <motion.div
                    initial={{ y: 20, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ delay: 0.5, duration: 0.8 }}
                >
                    <Text size="xl" c="dimmed" style={{ maxWidth: 660, marginBottom: '3rem', fontSize: '1.5rem', lineHeight: 1.5 }}>
                        Connecting people through premium broadcasting facilities and seamless scheduling.
                    </Text>
                </motion.div>

                <motion.div
                    initial={{ y: 20, opacity: 0 }}
                    animate={{ y: 0, opacity: 1 }}
                    transition={{ delay: 0.8, duration: 0.8 }}
                >
                    <Group>
                        <Button component={Link} href="/facilities" size="xl" radius="xl" variant="filled" color="dark" style={{ paddingLeft: 40, paddingRight: 40 }}>
                            시설 예약하기
                        </Button>
                        <Button component={Link} href="/schedule" size="xl" radius="xl" variant="default" style={{ paddingLeft: 40, paddingRight: 40 }}>
                            일정 확인하기
                        </Button>
                    </Group>
                </motion.div>
            </Box>

            {/* Features Section */}
            <Container size="lg" py={80}>
                <SimpleGrid cols={{ base: 1, sm: 2 }} spacing={40}>
                    <motion.div
                        initial={{ opacity: 0, x: -20 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.6 }}
                        viewport={{ once: true }}
                    >
                        <Card padding={40} radius="xl" withBorder style={{ height: '100%', borderColor: 'rgba(0,0,0,0.05)', backgroundColor: 'rgba(255,255,255,0.5)', backdropFilter: 'blur(10px)' }}>
                            <Card.Section mb="xl">
                                <div style={{ width: 80, height: 80, background: '#f8f9fa', borderRadius: '24px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                    <IconBuildingCommunity size={40} color="#000" stroke={1.5} />
                                </div>
                            </Card.Section>
                            <Title order={3} size="h2" fw={700} mb="sm">State-of-the-art Facilities</Title>
                            <Text c="dimmed" size="lg" style={{ lineHeight: 1.6 }}>
                                Experience our world-class auditorium and conference rooms equipped with the latest broadcasting technology.
                            </Text>
                        </Card>
                    </motion.div>

                    <motion.div
                        initial={{ opacity: 0, x: 20 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.6, delay: 0.2 }}
                        viewport={{ once: true }}
                    >
                        <Card padding={40} radius="xl" withBorder style={{ height: '100%', borderColor: 'rgba(0,0,0,0.05)', backgroundColor: 'rgba(255,255,255,0.5)', backdropFilter: 'blur(10px)' }}>
                            <Card.Section mb="xl">
                                <div style={{ width: 80, height: 80, background: '#f8f9fa', borderRadius: '24px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                    <IconCalendar size={40} color="#000" stroke={1.5} />
                                </div>
                            </Card.Section>
                            <Title order={3} size="h2" fw={700} mb="sm">Easy Scheduling</Title>
                            <Text c="dimmed" size="lg" style={{ lineHeight: 1.6 }}>
                                Check availability and book your events seamlessly with our integrated scheduling system.
                            </Text>
                        </Card>
                    </motion.div>
                </SimpleGrid>
            </Container>
        </main>
    );
}
